  <script>
  import PerfCard from "@/components/PerfCard.vue";
import supabase from "@/supabase.js"; // Ruta correcta según tu proyecto
import { onMounted, ref } from "vue";
  
  export default {
    name: "PerfContainer",
    components: { PerfCard },
    setup() {
      const habConData = ref([]);
      const compsData = ref([]);
      const isLoading1 = ref(true);  // Estado de carga
      const isLoading2 = ref(true);  // Estado de carga
      
  
      const fetchData1 = async () => {
        // Obtener datos de la tabla "hab_con"
        const { data: habConRows, error: habConError } = await supabase.from("hab_con").select("*");
        if (habConError) console.error("Error fetching hab_con data:", habConError);
        else habConData.value = habConRows;

        isLoading1.value = false;  // Datos cargados, cambiar estado
        
        
      };

      const fetchData2 = async () => {
        // Obtener datos de la tabla "comps"
        const { data: compsRows, error: compsError } = await supabase.from("comps").select("*");
        if (compsError) console.error("Error fetching comps data:", compsError);
        else compsData.value = compsRows;
        isLoading2.value = false;  // Datos cargados, cambiar estado
      };
  
      onMounted(fetchData1);
      onMounted(fetchData2);
      
      
      return {
        habConData,
        compsData,
        isLoading1,
        isLoading2
        
      };
    },
    data() {
      return {
        audio: new Audio(require('@/assets/multimedia/multimedia/swipe.mp3')), // Cargar el audio en el data
        isPlaying: false,
      };
    },
    methods:{
      playAudio() {
        if (!this.isPlaying) {
          this.audio.play(); // Reproduce el audio solo si no se está reproduciendo
          this.isPlaying = true; // Marca que el audio se ha reproducido
        }
      },
      stopAudio() {
        this.audio.pause(); // Detiene el audio
        this.audio.currentTime = 0; // Reinicia el audio al principio
      },
      resetAudio() {
        this.isPlaying = false; // Resetea la bandera cuando el mouse sale de la tarjeta
      }
    },
  };
  </script>
  
